*{
  margin: 0;
  padding: 0;
}
*, *:before, *:after {
  box-sizing: inherit;
} 
:root{
  --color-primario: #1ca8be;
  --color-secundario: rgba(5,17,23);
  --color-sencundario-light: rgba(5,7,23,0.6);
  --blanco: #fff;
  --gris: #e1e1e1;
  --text-color: #6a777c;
  --dark: #18191a;
}
html {
  box-sizing: border-box;
  height: 100%;
  font-size: 62.5%; 
  min-height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
section{ top:0;}
body {
  font-family: 'Poppins', sans-serif;
  min-height: 100%;
}
.dark { background-color: #18191a; background-color: var(--dark);}
img {width: 100px; height: 100px;}
h1{font-weight: 400;}
a {text-decoration:none; text-align: center;}
.section { padding:2rem 0 4rem;display: flex;align-items: center; justify-content:center; flex-direction: column;}
h2 {font-size: 3rem; font-family: 'Poppins', sans-serif;}
h3 {font-size: 2.5rem; color:#6a777c; color:var(--text-color);font-weight:100;}
h4 { font-size: 1.5rem; font-weight: 400;}
p { font-size: 1.5rem; color: #6a777c; color: var(--text-color)}
label {display: block; margin-bottom: 1.5rem;}
form { border: 1px solid #000; padding: 2rem; margin-bottom: 4rem; border-radius: 1rem;}
input,textarea {border: none; width: calc(100% - 1rem);padding-top: 1rem; padding-bottom: 1rem; padding-left: .5rem;padding-right: 0.5rem; ;border-radius: 1.8rem; background-color: #e1e1e1; background-color: var(--gris); outline: none; margin-bottom: 1rem;}
/*NAV*/
.logo { font-size: 2rem; color:rgba(0, 0, 0, 0.5)}
.logo:hover {color: #1ca8be;color: var(--color-primario);}
.nav {display: flex; justify-content:space-between; z-index: 1;position: fixed; bottom: 0; align-items: center;box-shadow: 1px -4px 10px rgba(0, 0, 0, 0.2); width: 100%; height: 90px; border-top-left-radius: 18px; border-top-right-radius: 18px; background-color: #fff; background-color: var(--blanco)}
.nav_logo { margin-left: 20px;}
.nav-icon { font-size: 1.8rem}
.nav_icons {justify-content:center; padding: 2rem;}
.nav-close { font-size: 3rem;position: absolute;
  right: 1.3rem;
  bottom: 0.5rem;
  cursor: pointer;}
.nav-open {font-size: 3rem; cursor: pointer;}
.nav-open:hover {color: #1ca8be;color: var(--color-primario)}
.md {font-size: 2.5rem; cursor: pointer;}
.md:hover {color: #1ca8be;color: var(--color-primario)}
ul { list-style: none;}
.nav-menu {position: fixed; left: 0; width: 100%; background-color: #fff; background-color: var(--blanco);padding : 3rem 1.5rem 8rem;box-shadow: 1px -4px 10px rgba(0, 0, 0, 0.2);transition: 0.5s ease;border-top-left-radius: 18px; border-top-right-radius: 18px; bottom: 0;}
.nav-menu-item { margin-right: 2rem; margin-left: 2rem; font-size: 1.5rem;}
li a { color: rgba(5,17,23); color: var(--color-secundario); }
.nav-link { width:20px; height: 20px;}
.nav-link:hover { color: #1ca8be; color: var(--color-primario); cursor: pointer;}
@media screen and (min-width: 769px){
  .nav { top: 0; border-top-left-radius: 0; border-top-right-radius: 0;box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.2); margin-bottom: 4rem;}
  .nav-menu-list { display: flex; justify-content: space-between;}
  .container { margin-top: 8rem;}
  .nav_icons { display: none;}
  .nav-menu { margin-right: 2rem; display: block!important; padding: 0; box-shadow: none; width: auto; width: initial; position:static; position:initial;}
  .nav-close {display: none;}
  .nav-icon {display:none;}
  .form-contact {display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;}
}
@media screen and (max-width: 767px) {
  /* .nav-menu { display: initial!important;} */
  .nav-menu-list {display: grid; grid-template-columns: repeat(3, 1fr); grid-gap:4rem; gap:4rem; padding-left: 2rem;}
  .nav-link {display: flex; flex-direction: column; align-items: center;}
}
.nav-menu { display: none;}
.nav-menu-mobile {    position: fixed;
  left: 0;
  width: 100%;
  background-color: #fff;
  background-color: var(--blanco);
  padding: 3rem 1.5rem 8rem;
  box-shadow: 1px -4px 10px rgb(0 0 0 / 20%);
  transition: 1s ease;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
  bottom: 0;
}
.nav-menu-list-mobile {    display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 4rem;
  grid-gap: 4rem;
  gap: 4rem;
  padding-left: 2rem;}
.nav-menu-item-mobile {margin-right: 2rem;
  margin-left: 2rem;
  font-size: 1.5rem;}
.mode {font-size: 2rem; cursor: pointer}
/*Presentacion*/
.container {display: grid; grid-template-columns: 2fr 3fr ;align-items: center; padding: 2rem;}
.presentation-blob { fill: #1ca8be; fill: var(--color-primario);width: 250px;}
.presentation-blob-img { width: 250px;}
.presentation-links {display: flex; flex-direction: column-reverse;}
.presentation-links a { color: #1ca8be; color: var(--color-primario); margin-top: 1.5rem; margin-right: 2rem;}
.presentation-links a:last-of-type { margin-top: 2rem;}
a ion-icon { font-size: 2.5rem!important;}
.presentation-info {grid-column: 1/3; margin-top: 1rem; text-align: center} 
.uil-message-contact { font-size: 1.5rem;}
.font-1 { font-size: 1.5rem;}
.presentation-p { margin-top: 2rem;}
@media screen and (min-width: 769px){
  .presentation-info { grid-column: 3/3; text-align: left;margin-right: 8rem; margin-left: 10rem;}
  .presentation-img { align-items: center;}
  .presentation-blob { width:300px;}
  .section { margin-top: 10rem;}
  .about-img { width: 80%!important; height: 100%!important;}
  .container.about {margin: 0;}
  .about-infor {    display: flex;justify-content: flex-end;align-items: center;flex-direction: row;width: 100%;}
  .about-infor-data {margin-right: 45rem;}
}
@media screen and (max-width: 768px) {
  .presentation-img { margin-right: 4.5rem;}
  .contact{flex-direction: column!important;}

}
/*Btn*/
.btn { padding: 1.5rem; margin-top: 5rem; border: none; display: inline-block;border-radius: 10px; color: #fff; color: var(--blanco); margin-bottom: 5rem;}
.btn-01 { background-color: #1ca8be; background-color: var(--color-primario);}
.btn-contact { float: right; margin: 0;}



/*About*/
.flex {display: flex!important;flex-direction: column;}
.title,.subtitle { text-align: center;}
.subtitle { font-size: 1.5rem; color: #6a777c; color: var(--text-color); text-align: center;}
.about-img { width: 250px; height: 250px; margin: 0 auto; display: flex; justify-content: center; padding: 0 2rem;}
.about-img img { width:100%; height: 100%; border-radius: 18px;box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.5);}
.about-description  p { font-size: 1.5rem; margin-bottom: 2rem;}
.about-data p { margin-top: 2rem; text-align: center;}
.about-info { display: flex; justify-content: space-evenly;}
.about-info .about-info-dt { display: flex; justify-content: center; align-items: center; flex-direction: column;}
.about-info .about-info-dt  span {font-size:2rem; font-weight: 500; margin-left: 2rem;}
.about-info .about-info-dt  p {font-size: 1.5rem; margin-top: 1rem;}
.about-btn { width:100%; align-items: center; display: flex; justify-content: center;}
.about-btn a,.about-btn a i { font-size: 1.3rem;}
.section.about .title { right: 0; }

/*Skilss*/
.skills { margin-bottom: 5rem;}
.skills-title, .skills-subtitle { text-align: center;}
.skills-titles{ display: flex; justify-content: space-between; align-items: center; margin-top: 2rem;margin-bottom: 1rem;}
.skills-icon { font-size: 3rem; margin: auto; margin-right: 2rem; color: #1ca8be; color: var(--color-primario)}
.skills-header {display: flex; justify-content: space-between; align-items: center; cursor: pointer;}
.skills-arrow { color: #1ca8be; color: var(--color-primario); font-size: 3rem; margin:auto; margin-left: 2rem;}
.skills-bar, .skills-percentage{ height:5px; border-radius: 5px;}
.skills-bar { background-color: rgba(5,7,23,0.6); background-color: var(--color-sencundario-light); width:100%; margin-bottom: 2rem;}
.skills-percentage { background-color: #1ca8be; background-color: var(--color-primario);display: block;}
.skills-m { margin: auto;}
.skills-html { width:90%;}
.skills-css {width: 70%}
.skills-figma { width: 50%}
.skills-JavaScript {width: 80%;}
.skills-Php{ width:70%;}
.skills-Photoshop {width: 70%}
@media screen and (min-width: 769px){
  .flex.container { display: grid!important;grid-column-gap: 50px}
  .about-btn { grid-column: 2/3;}
  .flex.skills {flex-direction: row; flex-wrap: wrap; justify-content: space-between; width: 100%;}
  .skills-content.one { margin: auto;}
  .contact-section {display: inline!important;display: initial!important;}
  
}
.section.skills {margin-bottom: 13rem;}
/*Sections*/
.section.about { background-color: #e1e1e1;}

/*Contact*/
.contact {width: 100%;margin-top: 1rem; font-size: 1.5rem; display: flex; flex-direction: row; align-items: center;}
.form-contact { padding:4rem;}
.contact-section {width: 50%; display: none;padding: 4rem;}
.contact-section-info { display: flex; flex-direction: column; justify-content: center; align-items: center;}
.form-two { display: flex; flex-direction: row; justify-content: space-between;}
form {box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.5);border: none;}
#apellido { margin-left: 1rem;}
#nombre {margin-right: 1rem;}
.contact-section-info img { width: 80%; height:100%;}

/*Carousel*/
.img-carousel {width: 80%; height: 80%;}

/*Portfolio*/
.portfolio {margin-bottom: 2rem;}
.carousel-wrapper {margin-bottom: 5rem;}



































/*Scroll*/
::-webkit-scrollbar {
  -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
  width: 8px;
}

::-webkit-scrollbar-button:increment,::-webkit-scrollbar-button {
  display: none;
} 

::-webkit-scrollbar:horizontal {
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #797979;
  border-radius: 10px;
  border: 2px solid #fff;
  border: 2px solid var(--blanco);
}

::-webkit-scrollbar-track { 
  background-color: var(--secundario);
}

